
// Some adjustable heights:
@form-height: 40px;
@form-margin: 5px;
@header-height: 30px;

// big/small image size in topic list.
@user-image-size: 76px;
@user-image-size-small: 56px;
@remaining-user-bubble-size: 28px;
@remaining-user-bubble-border: 3px;
@remaining-user-bubble-bg: white;
@remaining-user-bubble-fg: green;

// user image size in message list.
@message-image-size: 48px; //@user-image-size-small;
@message-arrow-size: 8px;
@message-arrow-offset: @message-image-size / 2;
@own-message-bg: #48930a;
@own-message-fg: white;
@foreign-message-bg: #d5d5d5;
@foreign-message-bg2: #b2b2b2; //border

// This is some paddings that are used troughout.
@default-padding: 6px;
@extended-padding: 2 * @default-padding;

// This is additional padding that compensates for the width and padding
// of a status bubble.
@status-compensation: 13px;

// This really should be pulled from somewhere else.
@message-preview-lineheight: 20px;
@preview-lines: 3;

.messagesView {
	.time {
		.detail-text();
		white-space: nowrap;
		text-align: right;
		text-overflow: ellipsis;
		overflow: hidden;
	}

	.me .time {
		color: #eee;
	}
}

#newmessage {
	// remove the unneeded border-right for mobile devices. Sadly not possible without an !imoportant.
	// Nils: it feels like the original border-right is too wide!
	.column(1, 1, left, 0);
	border-right: none !important;
	&.topic {
		border: none;
		background: transparent;
		.btn {
			width: 100%;
			border: none;
			border-radius: 0;
		}
	}
}

#topicListWrap {
	.column(1, 1, left, 0);

	height: 100%;
	overflow-y: scroll;
	overflow-x: hidden;
	box-sizing: border-box;
	border-bottom: @header-height solid transparent;
	border-right: 1px solid #ccc;
}

.imgWrap {
	float: left;
	width: 64px;
	height: 64px;

	margin-left: -52px;
	margin-right: 24px;

	overflow: hidden;
	border-radius: 100%;
}

.userimg {
	width: 100%;
}

.userimg--group {
	float: left;
	width: 50%;
	height: 50%;
}

.userimg--remaining {
	font-size: 0.75em;
	padding: 0 7px;

	line-height: 30px;
	background-color: #575757;
	color: #fff;

	box-sizing: border-box;
}

#topicList {
	.topic {
		border-bottom: 1px solid #ddd;
		cursor: pointer;
		position: relative;
		padding-right: @extended-padding;
		.left {
			.userimg {
				width: 80px;
				border: 1px solid #f2f2f2;
				border-radius: 100%;
			}
		}
		.username {
			padding-top: @default-padding;
			padding-left: 0;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
		}
		.lastMessage {
			.small-text();
			color: #555;
			height: (@preview-lines * @small-line-height); // three lines of text.
			overflow: hidden;
			padding-left: @status-compensation;
			padding-right: @extended-padding;
		}
		&:hover, &.unread, &.active {
			background-color: @primaryLighter;
			.username {
				color: #ececec;
			}
			.time {
				color: #eee;
			}
			.lastMessage {
				color: #eee;
			}
		}
		&.unread, &.active {
			&:hover {
				background-color: @primaryLightest;
			}
		}
		&.groupChat {
			.left {
				.imgWrap {
					// group chats have smaller images, and compensate for equal
					// height of the list element w/ some bottom padding.
					padding-bottom: (@user-image-size - @user-image-size-small + @default-padding);
					padding-right: (@user-image-size - @user-image-size-small + @default-padding);
					img {
						box-sizing: content-box;
						width: @user-image-size-small;
						height: @user-image-size-small;
					}
					img ~ img {
						// offset the second image
						position: absolute;
						top: (@default-padding + @user-image-size - @user-image-size-small);
						left: (@default-padding + @user-image-size - @user-image-size-small);
					}
					span {
						// the span with remaining users is missing a class.
						// trust me, it's this one :-)
						position: absolute;

						box-sizing: content-box;
						width: @remaining-user-bubble-size;
						height: @remaining-user-bubble-size;

						left: (@default-padding + @user-image-size - @remaining-user-bubble-size - @remaining-user-bubble-border);
						top: (@default-padding + @user-image-size - @remaining-user-bubble-size - @remaining-user-bubble-border);
						border: @remaining-user-bubble-border solid @remaining-user-bubble-fg;
						border-radius: 100%;

						text-align: center;
						line-height: @remaining-user-bubble-size;
						.bold-text();

						color: @remaining-user-bubble-fg;
						background: @remaining-user-bubble-bg;

						// typography, this should probably be handled elsewhere.
						text-decoration: italic;
					}
				}
			}
		}
	}
}

#topicWrap {
	height: 100%;
	overflow: hidden;
	position: relative;
}

#topicWrap.newMessage {
	padding: @default-padding @default-padding 0;
	.box-fix;
}

.newMessage #messageTitle h2 {
	margin-top: 0;
}

#messageWrap {
	height: 100%;
	// this makes absolute positioned elements adopt the padding.
	position: relative;
}

#messageList {

	// this is for well-behaving w/ the form at the bottom.
	box-sizing: border-box;
	//border-bottom: (@form-height + @header-height + 2 * @form-margin) solid transparent;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: @form-height + @header-height + 2 * @form-margin;
	margin: 0;
	padding: 0;

	.burst {
		float: left;
		clear: both;
		min-width: 80%;
		max-width: 100%;
		word-wrap: break-word;
		&:first-child {
			// additional margin at the top, this will be useful when we have a
			// 'pull down to load more' feature.
			margin-top: @extended-padding;
		}
	}

	.username {
		.detail-text();
		padding-bottom: @default-padding / 2;
		text-transform: uppercase;
	}

	.username, .userimg {
		margin-left: @extended-padding;
		margin-right: @extended-padding;
	}

	.userimg, {
		display: inline-block;
		float: left;
		clear: both;
		img {
			border-radius: 100%;
			width: @message-image-size;
			height: @message-image-size;
		}
	}

	// this is the messages container.
	.messages {
		// angular creates and fills w/o moving stuff into display: none,
		// so we need to add min size to prevent constant UI movement.
		min-height: @message-image-size - 2 * @default-padding;
		min-width: @message-image-size - 2 * @extended-padding;
		margin-left: @message-image-size + 2 * @extended-padding;
		margin-right: @message-image-size + 2 * @extended-padding;
		padding: @default-padding @extended-padding;
		margin-bottom: @extended-padding;
		margin-top: 0;
		border-radius: @extended-padding;
		background: @foreign-message-bg;
		border: 1px solid @foreign-message-bg2;
		position: relative;
	}

	.messages:after, .messages:before {
		right: 100%;
		top: @message-arrow-offset;
		border: solid transparent;
		content: " ";
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	.messages:after {
		border-color: fade(@foreign-message-bg, 0%);
		border-width: @message-arrow-size;
		margin-top: -1 * @message-arrow-size;
	}
	.messages:before {
		border-color: fade(@foreign-message-bg2, 0%);
		border-width: 1px + @message-arrow-size;
		margin-top: -1 * (1px + @message-arrow-size);
	}

	// messages from others
	.other {
		.messages:after {
			border-right-color: @foreign-message-bg;
		}
		.messages:before {
			border-right-color: @foreign-message-bg2;
		}
	}

	// customizations for messages by me
	.me {
		&.burst {
			float: right;
		}
		.userimg {
			float: right;
		}
		.username {
			text-align: right;
		}
		.messages {
			background-color: @own-message-bg;
			color: @own-message-fg;
		}
		.messages:after, .messages:before {
			left: 100%;
		}
		.messages:after {
			border-color: fade(@own-message-bg, 0%);
			border-left-color: @own-message-bg;
		}
		.messages:before {
			border-left-color: @foreign-message-bg2;
		}
		a {
			color: @own-message-fg;
		}
	}

}

.messageView-loadingText {
	float: left;
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
	width: 100%;
	height: 50px;
	line-height: 50px;
	padding: 0 0.5%;
	box-sizing: border-box;
	margin: 0;
	background: rgba(0,0,0,0.8);
	color: #fff;
	z-index: 9;
}

.messageView-loadingText--relative {
	position: relative;
}

#messageView-loadingIcon {
	vertical-align: middle;
	display: inline-block;
}

#inputWrap { // this is the form
	display: block;
	position: absolute;
	width: 100%;
	bottom: 0;
	margin-bottom: @form-margin;

	textarea {
		height: @form-height;
		.column(3,4);
	}
	button {
		height: @form-height;
		.column(1,4);
	}
}
.newMessage #inputWrap {
	textarea {
		.width(3,4,1%);
		margin: 0 1% 0 0;
	}
	button {
		.width(1,4,1%);
		margin: 0 0 0 1%;
	}
}